@media (max-width: 767px) {
  .navigation__toggle {
    position: absolute;
    top: 18px;
    right: 18px;
    z-index: 2;
    display: block;
    width: 28px;
    height: 24px;
    background-color: white;
    cursor: pointer;
    border: none;
    background-color: #2f76e9; }
  .navigation--closed .navigation__toggle::before {
    content: "";
    position: absolute;
    top: 5px;
    left: 2px;
