    height: 3px;
    background-color: #000000;
    box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000; }
  .navigation--closed .navigation__toggle:active::before {
    background-color: rgba(255, 255, 255, 0.3);
    box-shadow: 0 8px 0 0 rgba(255, 255, 255, 0.3), 0 16px 0 0 rgba(255, 255, 255, 0.3); }
  .navigation--opened .navigation__toggle {
    top: 25px;
    right: 18px; }
    .navigation--opened .navigation__toggle::before, .navigation--opened .navigation__toggle::after {
      content: "";
      position: absolute;
      top: 5px;
      left: 2px;
      width: 24px;
      height: 3px;
      background-color: #000000; }
    .navigation--opened .navigation__toggle::before {
      transform: rotate(45deg);
      box-shadow: none; }
    .navigation--opened .navigation__toggle::after {
      transform: rotate(-45deg); }
    .navigation--opened .navigation__toggle:active::before, .navigation--opened .navigation__toggle:active::after {
      background-color: rgba(255, 255, 255, 0.3); }
  .navigation__wrapper {
    width: 100%; }
  .navigation__list {
    width: 100%;
