.modal {
  position: absolute;
  width: 300px;
  height: auto;
  display: flex;
  border: 2px solid black;
  display: flex;
  background: #4085f6;
  border-radius: 10px;
  box-shadow: 0 0 26px 0 rgba(0, 0, 0, 0.75);
  padding: 20px;
  width: 80%; }

.modal_container {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  align-items: center; }

.modal-title {
  margin-top: 70px;
  font-size: 24px;
  color: white; }

.modal_button-close--long {
  border: 2px solid white;
  color: white;
  background-color: #4085f6;
  font-size: 25px;
  width: 85%;
  height: 70px;
  border-radius: 50px; }
  .modal_button-close--long:hover {
    opacity: 0.5; }

.modal_button-close--rownd {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  position: absolute;
  top: 5px;
  right: 5px;
  background-color: #4085f6;
  border: 2px solid white; }
  .modal_button-close--rownd:hover {
    opacity: 0.5; }

.modal_button-close--rownd::before,
.modal_button-close--rownd::after {
  content: "";
  position: absolute;
  top: 4px;
  width: 10px;
  height: 39px; }

.modal_button-close--rownd::before {
  left: 22px;
  border-left: 2px solid white;
  transform: rotate(-45deg); }

.modal_button-close--rownd::after {
  left: 17px;
  border-right: 2px solid white;
  transform: rotate(45deg); }

